@charset "UTF-8";
.w {
  width: 1210px;
  margin: 0 auto; }

body {
  font-family: "Helvetica Neue",Helvetica, "微软雅黑", Arial, Helvetica, sans-serif;
  font-size: 12px; }

a:link,
a:visited,
a:active,
a:focus {
  color: #666;
  text-decoration: none; }
  a:link:hover,
  a:visited:hover,
  a:active:hover,
  a:focus:hover {
    color: #c81623;
    color: #f00f0f; }

.one_inner-toolbar {
  height: 30px;
  background: #f1f1f1;
  color: #666; }
  .one_inner-toolbar .welcome {
    float: left;
    display: inline-block;
    font-family: Arial, "微软雅黑",  Helvetica, sans-serif; }
    .one_inner-toolbar .welcome span {
      line-height: 30px; }
  .one_inner-toolbar .sub-nav {
    float: right; }
    .one_inner-toolbar .sub-nav > li {
      float: left;
      padding: 0 2px;
      line-height: 30px; }
    .one_inner-toolbar .sub-nav > li:last-child {
      padding: 0 8px;
      margin-right: 30px; }

.one_inner-search .logo-wrap {
  position: relative;
  z-index: 12;
  float: left;
  width: 362px;
  height: 60px;
  padding: 20px 0;
  box-sizing: content-box; }
  .one_inner-search .logo-wrap > .logo {
    display: block;
    width: 270px;
    height: 60px; }

.one_inner-search .qrcode {
  float: right;
  position: relative;
  top: 0;
  right: 40px;
  z-index: 11;
  height: 100px;
  width: 100px;
  margin-top: -55px;
  margin-right: 65px;
  background: url(/images/qrcode.png) no-repeat center 20px;
  border: 1px solid #ddd;
  border-radius: 2px;
  box-sizing: border-box;
  background-size: 75px 75px; }
  .one_inner-search .qrcode > p {
    color: #b61d1d;
    padding-top: 2px; }

.one_inner-search .search {
  margin-top: 25px;
  position: relative;
  z-index: 11;
  float: left;
  width: 540px; }
  .one_inner-search .search .text {
    float: left;
    height: 24px;
    line-height: 24px;
    color: #666;
    padding: 4px;
    width: 446px;
    margin-bottom: 4px;
    border: 2px #b61d1d solid;
    border-right: 0;
    outline: 0;
    font-size: 14px;
    box-sizing: content-box; }
  .one_inner-search .search .button {
    float: left;
    width: 82px;
    height: 36px;
    background: #b61d1d;
    border: none;
    line-height: 1;
    color: #fff;
    font-size: 16px;
    cursor: pointer; }

.one_inner-search .hotwords {
  float: left;
  width: 462px;
  height: 20px;
  line-height: 20px;
  overflow: hidden; }
  .one_inner-search .hotwords > a {
    white-space: nowrap;
    margin-right: 10px;
    float: left; }

.one_inner-nav {
  height: 46px;
  border-bottom: 2px solid #b1191a; }
  .one_inner-nav li {
    float: left; }
    .one_inner-nav li .return-index {
      display: block;
      width: 190px;
      height: 44px;
      padding: 0 10px;
      background: #b1191a;
      font: 400 15px/44px "microsoft yahei";
      color: #fff;
      text-decoration: none;
      font-size: 17px;
      text-align: center; }
      .one_inner-nav li .return-index:hover {
        color: #f5f5f5; }
    .one_inner-nav li > a {
      height: 44px;
      padding: 0 20px;
      text-align: center;
      text-decoration: none;
      font: 400 15px/44px "microsoft yahei";
      color: #333; }
